import { useContext } from 'react'
import { ShopContext } from '../Context/context'
import BasketItem from './BasketItem'

const BasketList = () => {
    const { order = [], isBasketShow, handleBasketShow } = useContext(ShopContext)
    const totalPrice = order.reduce((sum, el) => sum + el.price * el.quantity, 0)

    const closeBasket = () => {
        document.querySelector('.modal-shop.open').classList.add('close')
        document.querySelector('.modal-shop.open').classList.remove('open')
        setTimeout(() => {
            document.querySelector('.modal-shop .collection').classList.add('hide')
            handleBasketShow()
            setTimeout(() => document.querySelector('.modal-shop .collection').classList.remove('hide'), 350);
        }, 500);
    }

    return (
        <div className={`modal-shop${isBasketShow ? " open" : ""}`}>
            <ul className="collection">
                <li className="collection-item active">ÐÐ¾ÑÐ·Ð¸Ð½Ð° <span className="secondary-content" onClick={closeBasket}><i className="material-icons">close</i></span></li>
                {order.length ? order.map((item, index) => <BasketItem key={item.id + String(index)} {...item} />) : <li className="collection-item">ÐÐ¾ÑÐ·Ð¸Ð½Ð° Ð¿ÑÑÑÐ°</li>}
                <li className="collection-item active">ÐÐ±ÑÐ°Ñ ÑÑÐ¾Ð¸Ð¼Ð¾ÑÑÑ: {totalPrice}</li>
            </ul>
        </div>
    )
}

export default BasketList
